import React, { useState, useEffect } from 'react';
import api from './services/api';

import './global.css';
import './App.css';
import './Sidebar.css';
import './Main.css';

import DevForm from './components/DevForm';
import DevItem from './components/DevItem';

function App() {
  const [devs, setDevs] = useState([]);
  const imgStats = [{
    _id: 0,
    imgSrc: 'https://github-readme-stats.vercel.app/api?username=ramonlimaramos&show_icons=true&hide_border=true&&count_private=true&include_all_commits=true'
  }, {
    _id: 1,
    imgSrc: 'https://github-readme-stats.vercel.app/api/top-langs/?username=ramonlimaramos&layout=compact'
  }]

  useEffect(() => {

    async function loadDevs() {
      const response = await api.get("/ramonlimaramos");
      response.data['techs'] = ['Go', 'Python', 'NodeJs'];
      setDevs(response.data);
    }

    loadDevs();
  }, []);

  // async function callbackHandleAddDev(data) {

  //   const response = await api.post('/devs', data);

  //   setDevs([...devs, response.data]);
  // }
  
  return (
    <div id="app">
      <aside>
        <DevForm dev={devs} />
        {/* <DevForm submit={callbackHandleAddDev} /> */}
      </aside>
      <main>
        <ul>
          {imgStats.map(stat => (  
            <DevItem key={stat._id} stat={stat} />
          ))}
        </ul>
      </main>
    </div>
  );
}
 
export default App;
